import React from 'react'
import Card from 'react-bootstrap/Card'

function NewsList({item}) {
    return (
        <div>
            <Card className="mx-auto my-4"style={{ width: '75%' }}>
  <Card.Body>
    <Card.Title>{item.title}</Card.Title>
    <Card.Subtitle className="mb-2 text-muted">{item.created_at}</Card.Subtitle>
    
    <Card.Link href={item.url}>{item.url}</Card.Link>
    
  </Card.Body>
</Card>
        </div>
    )
}

export default NewsList
