import 'bootstrap/dist/css/bootstrap.min.css';
import { Container } from 'react-bootstrap';
import './App.css';
import {useEffect,useState} from 'react';
import axios from 'axios';
import InputForm from './Component/InputForm'
import NewsList from './Component/NewsList'




function App() {
  
  const [submitRequest,setSubmitRequest] = useState('')
  const [inputQuery,setInputQuery] = useState('')
  const [news,setNews] = useState([]);

  useEffect(()=>{
  getNews()
},[submitRequest])

const getNews = () => {
 axios.get(`https://hn.algolia.com/api/v1/search?query=${submitRequest}`)
  .then(response => response.data.hits)
  .then(info => setNews(info))
  
  .catch(err => console.log(err))
}

const handleChange=(e)=>{
  setInputQuery(e.target.value)
}

const handleClick=()=>{
  setSubmitRequest(inputQuery)
}

  return (
    <Container>
    <div className="App">
      <h1 className="text-center my-3">NEOS</h1>
      <InputForm handleChange={handleChange} handleClick={handleClick}/>
      {news.map(item => {
        return <NewsList item={item} key={item.objectID}/>
      })}
    </div>
    </Container>
  );
}

export default App;
