import React from 'react'
import {InputGroup,FormControl,Button} from 'react-bootstrap'

function InputForm({handleChange,handleClick}) {
    return (
        <div>
            <InputGroup className="mb-3 mx-auto">
    <FormControl
      aria-label="Search"
      aria-describedby="basic-addon2"
      onChange={handleChange}
      
    />
    <InputGroup.Append>
      <Button variant="outline-primary" onClick={handleClick}><i class="fas fa-search"></i></Button>
    </InputGroup.Append>
  </InputGroup>
        </div>
    )
}

export default InputForm
